@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
html{
  padding:0;margin:0;
}
body{
  margin:0;padding:0;
  background-color: #3C3D3D;
}

.work{
    width:90%;margin:0 auto;
}
.work h1{text-align:center;font-family:"Century Gothic",sans-serif;color:#FFBCBC;margin-bottom:20px;}
.projects{
    display:grid;width:90%;margin:0 auto;
    grid-template-columns: repeat(3,1fr);   grid-template-rows: repeat(2,180px);
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;grid-row-gap:15px;row-gap:15px;margin-bottom:5px;transition:opacity  1s;-webkit-animation:project 1s 1 ease-in-out;animation:project 1s 1 ease-in-out;
}
.projects div{
    box-shadow:5px 5px 6px rgb(19,18,18),-5px -5px 6px rgb(37,37,37);/* border-radius:5px; */overflow: hidden;position:relative;
    border:5px solid rgb(100, 99, 99);cursor:pointer;
}
.projects div img{
    width:100%;height:100%;opacity:0.9;transition:all 0.3s ease-in-out;
}
.projects div p{
    position: absolute;background-color: rgba(0,0,0,0.8);bottom:0;width:100%;margin:0;padding:10px;text-align:center;color:white;font-family:monospace;transition:all 0.3s ease-in-out;
}
@-webkit-keyframes project{
    from{
        opacity:0;
    }
    to{
        opacity:1;
    }
}
@keyframes project{
    from{
        opacity:0;
    }
    to{
        opacity:1;
    }
}
.projects div button{
    color: white;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    background-color:grey;position: absolute;opacity:1;
    margin:auto;top:0;bottom:0;left:0;right:0;
    border:2px solid white;width:80%;background-color:rgba(0,0,0,0.8);color:white;font-family: monospace;padding:5px;
    height:40px;border-radius:5px;opacity:0;transition:opacity 0.3s ease-in-out;


}
/* .projects div button span{

} */
.projects div:hover img{
    transform:scale(1.1);
}
.projects div:hover p{
    bottom:135px;
}
.projects div:hover img{
opacity:0.5;
}
.projects div:hover button{
    opacity:1;transition-delay: 0.3s;
}
.projects div button:hover{
    background-color:rgb(71, 69, 69);border:2px solid black;
}
@media screen and (max-width:900px){
.work{width:unset;}
.projects{
    grid-template-columns: repeat(2,1fr);   grid-template-rows: repeat(3,180px);
}
}
@media screen and (max-width:600px){
    .projects{
        grid-template-columns: repeat(1,1fr);   grid-template-rows: repeat(6,200px);
    }
    .projects div:hover p{
        bottom:155px;
    }
    }
.header{
    display:flex;
    padding:20px 60px;
    justify-content: space-between;
    align-items: center;
    font-family:"Century Gothic",sans-serif;font-weight:400;
    background: transparent;
}
.headerName{
    font-size:40px;color:#FFBCBC;font-family: 'Satisfy', cursive;
}
.headernamelink{
    flex:1 1;
}
.links{
    text-decoration: none;color:white;
}
.headerlinks{
   flex:1 1;position:relative;
}
.smallsizebutton{display:none;margin:0;padding:0;text-align:right;color:white;width:100%;}
.headerul{
    text-decoration: none;
    padding:0;margin:0;
list-style-type:none;display:flex;justify-content:space-between;
}
.headerlist{
    font-family:"Century Gothic",sans-serif;
    font-size:20px;
}/* .smallsizebutton:hover + .headerul{
    display:flex;flex-direction: column;background-color:white;color:black;padding:5px;position: absolute;right:0px;top:30px;
    } */
@media screen and (max-width:750px){
.smallsizebutton{display:inline-block;}
.headerlinks{display:flex;justify-content: flex-end;align-items: center;}
.headerul{
    display:none;
}
.header{
    padding:20px 20px;
}
}
.homecontent{
    width:70%;
    display:flex;
    align-items:center;
    justify-content:space-between;margin:0 auto;
}
.homeimg{
flex:1 1; -webkit-animation:imageeffect 1s 1 ease-in-out; animation:imageeffect 1s 1 ease-in-out;transition:translate opacity 1s;
}
@-webkit-keyframes imageeffect{
    from{opacity:0;
        transform:translate(50px,0px)
    }
    to{opacity:1;
        transform:translate(0px,0px)
    }
}
@keyframes imageeffect{
    from{opacity:0;
        transform:translate(50px,0px)
    }
    to{opacity:1;
        transform:translate(0px,0px)
    }
}
.homedivimg{
    border-radius:50%;overflow: hidden;
    border:8px solid #555555;
   box-shadow: 5px 5px 6px rgba(255, 255, 255, 0.034);height:250px;width:250px;margin:0 auto;
}
.homeintro{
flex:3 1;
transition:translate opacity 1s;
-webkit-animation:texteffect 1s 1 ease-in-out;
        animation:texteffect 1s 1 ease-in-out;
}
@-webkit-keyframes texteffect{
    from{opacity:0;
        transform:translate(-50px,0px)
    }
    to{opacity:1;
        transform:translate(0px,0px)
    }
}
@keyframes texteffect{
    from{opacity:0;
        transform:translate(-50px,0px)
    }
    to{opacity:1;
        transform:translate(0px,0px)
    }
}
.homeintro p{
    font-family:"Century Gothic",sans-serif;text-align:center;font-size:40px;margin:3px;color:white;
}
.name::before{
    content:"I'm";
    font-size:20px;
    padding-right: 10px;
    color:#989696;
}
.Buttoncontainer{
    display:flex;
    justify-content: center;
    
}
@media screen and (max-width:1000px){
    .homecontent{width:90%;}
}
@media screen and (max-width:750px){
    .homecontent{flex-direction:column;width:90%;}
    .homeimg,.homeintro{flex:1 1}
    .homeintro{padding-top:30px}
}
@media screen and (max-width:450px){
    .homeintro p{
        font-size:30px;
    }
}
.buttons{
    background-color:rgba(104,104,104,93%);
    border:3px solid white;
    border-radius:20px;
    font-family: "Century Gothic",sans-serif;
    box-shadow:2px 5px 6px rgba(0,0,0,47%);
    padding:10px;color:white;width:150px;text-align:center;margin:20px 20px;cursor:pointer;
}
@media screen and (max-width:450px){
    .buttons{
        margin:20px 5px;width:110px;
    }
}

