.contact{
    display:flex;
    flex-direction: column;
    font-family:"Century Gothic",sans-serif;
    width:80%;margin:0 auto;
    text-align:center;transition:translate opacity 1s;
    -webkit-animation:contact 1s 1 ease-in-out;
            animation:contact 1s 1 ease-in-out;
}
@-webkit-keyframes contact{
    from{opacity:0;
        transform:translate(0px,100px)
    }
    to{opacity:1;
        transform:translate(0px,0px)
    }
}
@keyframes contact{
    from{opacity:0;
        transform:translate(0px,100px)
    }
    to{opacity:1;
        transform:translate(0px,0px)
    }
}
.contact h1{
    color:rgb(255,175,175);font-weight: normal;
}
.contactmiddle{
    display:flex;
    flex-direction: column;
}
*:focus {
    outline: none;
}
.contactforms{
    display:flex;
    justify-content: space-between;
}
.inputs{
    flex:1 1;margin:20px 0;
}
.inputs input{
    background-color: transparent;
    padding:5px;border:none;
    border-bottom:3px solid rgb(206,206,206);
    font-family:"Century Gothic",sans-serif;font-size:18px;color:white;
}
.msgbox{
    width:70%;margin:0 auto;height:100px;border:3px solid rgb(206,206,206);padding:10px;background-color:transparent;    font-family:"Century Gothic",sans-serif;color:white;font-size:18px;
}
.button{
    padding:10px;
    background:linear-gradient(to right,rgb(249,140,140),rgb(155,150,150));
    border-radius:10px;
    border:none;
    width:150px;margin:10px auto;color:white;font-weight:bold;
}
@media screen and (max-width:600px){
    .contactforms{
        flex-direction: column;align-items: center;
    }
    .inputs{width:100%;}
    .msgbox{width:80%}
    .inputs input{width:90%;margin:0 auto;}
    .contact h1{font-size:25px;}
    }
.about{
    width:90%;
    display:flex;margin:0 auto;
}
.left{flex:1 1;}
.leftcontent{
    width:70%;height:400px;background-color: rgb(75,74,74);padding:20px;position:relative;
}
.left h1{
    color:#FFBCBC;font-size:40px;margin:0;font-family:"Century Gothic",sans-serif;padding:0 0 10px 20px;
}
.right{
    flex:1 1;padding-left: 50px;
}
.right h1{
    font-family:"Century Gothic",sans-serif;
   margin:0;color:#FFBCBC;
}
.right p{
    font-family:"Century Gothic",sans-serif;letter-spacing: 1px;
   margin:0;color:white;text-align:justify;padding-top:40px;line-height:1.6
}
.leftcontentinner{
    position:absolute;top:0;bottom:0;right:-100px;margin:auto 0;
    height:80%;width:200px;transition:translate opacity 1s;
    -webkit-animation: imgmove 1s 1 ease-in-out;
            animation: imgmove 1s 1 ease-in-out;
}
.wave{
    width:70%;
}
@-webkit-keyframes imgmove{
    from{
        right:100px;opacity:0;
    }
    to{
        right:-100px;opacity:1;
    }
}
@keyframes imgmove{
    from{
        right:100px;opacity:0;
    }
    to{
        right:-100px;opacity:1;
    }
}
@media screen and (max-width:1000px){
    .about{flex-direction:column;align-items:center;}
    .right{
        padding-top:40px;padding-left:0px;
    }
}
@media screen and (max-width:500px){
    .leftcontent{
    padding-left: 10px;width:80%;height:300px;
    }
    .left h1{font-size:25px;padding-left:10px;}
    .leftcontentinner{right:-30px;bottom:10px;margin:0;height:80%;top:unset}
    .right h1{
        font-size:25px;
    }
    .right p{letter-spacing: 0px;}
    .wave{
        visibility: hidden;
    }
    @-webkit-keyframes imgmove{
        from{
            right:100px;opacity:0;
        }
        to{
            right:-30px;opacity:1;
        }
    }
    @keyframes imgmove{
        from{
            right:100px;opacity:0;
        }
        to{
            right:-30px;opacity:1;
        }
    }
}
