.home{
    display:flex;
    justify-content: space-between;
    min-height:100vh;
    flex-direction: column;min-width:320px;
}
.bottombutton img{
    display:block;margin:0 auto;
    transition:translate opacity 1s;-webkit-animation:scrolldown 1s infinite ease-in-out;animation:scrolldown 1s infinite ease-in-out;cursor: pointer;
}
@-webkit-keyframes scrolldown{
    0%{
        opacity:1;
        transform:translate(0px,0px);
    }
    50%{
        opacity:0;
       transform:translate(0px,10px);
    }
    100%{
        opacity:1;
        transform:translate(0px,0px);
    }
}
@keyframes scrolldown{
    0%{
        opacity:1;
        transform:translate(0px,0px);
    }
    50%{
        opacity:0;
       transform:translate(0px,10px);
    }
    100%{
        opacity:1;
        transform:translate(0px,0px);
    }
}
.bottombutton{
    padding-bottom:20px;
}

